.app{
  background-image: url('background.jpg');
  background-size: auto;
  background-position: bottom;
  background-repeat: no-repeat;
}
main{
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0.2), rgba(0, 0, 0,  0.75));
  opacity: 20;
  min-height: 100vh;
}
input{
  border:none;
  width:95%;
  background: rgba(0, 0, 0,  0);
  text-align: center;
}
.len{
  width:60%;
  transition: 0.5s ease;
  display:flex;
  border:none;
  min-height: 7vh;
  font-size: 3vh;
  position: relative;
  margin-left: 7vh;
  text-align:center;
  border-radius: 0 0 20px 20px;
  background: rgb(235, 235, 235);
  box-shadow: 0px 5px rgba(0, 0, 0, 0.2);
}
.len button{
  border:none;
  background: rgba(0, 0, 0,  0);


}
.well{
  display:flex;
}
.name-text{
  margin-left:7vh;
  color: white;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  text-shadow: 2px 2px rgb(68, 68, 68);
  margin-top: 10vh;
}
.well{
  margin-left: 7vh;
  margin-top: -4vh;
  color:white;
  text-shadow: 2px 2px rgb(68, 68, 68);
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}
.cell-text{
  color:white;
  text-shadow: 2px 2px rgb(68, 68, 68);
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-size: 10vh;
  font-weight: 700;
}
.container-2{
  background:rgba(255,255,255,0.2);
  width:max-content;
  padding: 30px;
  border-radius: 50px;
  margin-left: 7vh;
  transition: 1s ease;

}
.container-2:hover{
  box-shadow: 0px 0px 10px rgb(68, 68, 68);
  transform: rotate(360deg);
  
}
.fully{
  animation-delay:0.5s;
}
.temp{
  font-size: 9vh;
  color:white;
  text-shadow: 2px 2px rgb(68, 68, 68);
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  margin-left: 7vh;
  font-weight: 700;;
}